  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.texto-negro {
  font-weight: bold;
}

/**Layout**/
header {
  width: 100%;
  height: 80px;
  background-color: #333;
}

.logo {
  height: 100%;
  display: flex;
  align-items: center;
}

.logo img {
  height: 50px;
  width: 100%;
}

.container__menu {
  max-width: 1200px;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
}

.menu {
  display: flex;
  align-items: center;
}
.menu ul {
  display: flex;
  padding: 0;
  margin-bottom: 0;
  margin-top: 0;
}
.menu ul li {
  list-style: none;
  display: flex;
  align-items: center;
  height: 40px;
  padding: 10px 10px;
}
.menu ul a {
  text-decoration: none;
  display: flex;
  font-size: 14px;
  color: #FFFFFF;
  text-transform: uppercase;
  transition: all 0.25s ease;
  flex: 1;
}

header .menu ul li:hover {
  background: #4175FC;
  border-radius: 20px;
}

.menu ul li:hover a {
  color: #fff; /* Opcional, cambia el color del texto cuando se hace hover en el li */
}

.menu nav img {
  display: none;
}

.menu #btn-menu {
  display: none;
}

/**Menu Responsive*/
@media (max-width: 1200px) {
  header {
    padding: 40px;
  }
}
@media (max-width: 720px) {
  .menu nav {
    position: fixed;
    top: 0;
    right: -250px;
    padding: 20px;
    background: #FFFFFF;
    width: 250px;
    height: 100vh;
    z-index: 1;
    transition: all 300ms;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .menu ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
  }
  .menu ul li {
    margin-top: 20px;
    margin-left: 0;
  }
  .menu ul li a {
    color: #bebebe;
    margin: 0;
  }
  .menu nav img {
    width: 150px;
    height: auto;
    display: block;
    margin: 0;
  }
  #back_menu {
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: none;
  }
  .menu #btn-menu {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    cursor: pointer;
    border-radius: 50px;
    transition: all 300ms;
  }
  .menu #btn-menu:hover {
    background: rgba(196, 184, 184, 0.2);
  }
}
#nav img {
  max-width: 90%;
  height: auto;
}

.no-webp .hero {
  background-image: linear-gradient(270deg, rgba(0, 0, 0, 0.5) 0%, rgba(18, 40, 87, 0.3) 100%), url(../img/banner.jpg);
}

.webp .hero {
  background-image: linear-gradient(270deg, rgba(0, 0, 0, 0.5) 0%, rgba(18, 40, 87, 0.3) 100%), url(../img/banner.webp);
}

.hero {
  height: 50vh;
  background-size: cover;
  background-position: center center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
@media (min-width: 768px) {
  .hero {
    height: 80vh;
  }
}

.contenido-hero {
  padding: 5rem;
}
.contenido-hero p {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.8rem;
  margin: 0;
}
.contenido-hero h2 {
  font-weight: bold;
  color: #FFFFFF;
  font-size: 4rem;
  margin: 1.5rem 0 1.5rem 0;
}
.contenido-hero span {
  font-size: 1.4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-hero p {
    font-size: 3rem;
    text-align: left;
  }
  .contenido-hero h2 {
    font-size: 6rem;
    text-align: left;
  }
  .contenido-hero span {
    font-size: 2.5rem;
    text-align: left;
  }
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.informacion-meta span {
  color: #E08709;
}

.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.imagen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.texto-entrada a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
.texto-entrada a span {
  color: #E08709;
  font-weight: bold;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.hero-entrada {
  position: relative;
  max-height: 20rem;
  min-height: 10rem;
  overflow: hidden;
  display: flex;
}
@media (min-width: 768px) {
  .hero-entrada {
    width: 100%;
    max-height: 45rem;
  }
}

.overlay-hero {
  position: absolute;
  background: rgb(2, 2, 18);
  background: linear-gradient(270deg, rgba(2, 2, 18, 0.5046393557) 100%, rgba(0, 0, 0, 0.3) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero-image picture {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .hero-image img {
    width: 100%;
    max-height: 60rem;
    object-fit: cover;
  }
}

.hero-text h2 {
  color: #FFFFFF;
  margin: 0;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .hero-text h2 {
    font-size: 5rem;
  }
}

/**Entrada Blog**/
.contenedor_principal {
  /*background-image: url(/img/motivo_cafe.jpg);
