}
@media (min-width: 768px) {
  .container_footer {
    flex-direction: row;
    justify-content: space-between;
  }
}
.container_footer p {
  color: #FFFFFF;
  margin: 0;
}

.logo_footer {
  padding: 1.8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.logo_footer img {
  width: 100px;
  height: auto;
}
.logo_footer a {
  display: flex;
}

.about-footer {
  text-align: center;
  margin: 0;
  padding: 1.8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about-footer .about-texto {
  font-weight: 300;
  font-size: 1.8rem;
}

.siguenos-footer {
  margin: 0;
