/**Galeria**/
.proyectos-recientes {
  padding: 20px;
}

.proyectos-recientes img {
  height: 100%;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  display: block;
  transition: all 0.2s ease;
}

.proyectos-recientes h3 {
  font-size: 2rem;
  margin: 0;
}

.proyectos-contenedor {
  padding-top: 60px;
  margin-bottom: 40px;
}

.overlay {
  transition: all 0.2s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.overlay p {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 0;
}

.proyecto:hover img {
  opacity: 0.2;
}

.proyecto:hover .overlay {
  opacity: 1;
}

.overlay .iconos-contenedor {
  display: flex;
}

.overlay i {
  color: black;
  font-size: 60px;
  margin: 10px;
}

.proyecto {
  position: relative;
}

.btn-info {
  font-size: 1.8rem;
  margin: 20px;
}

.boton-info {
  display: flex;
  justify-content: center;
}

.card-section {
  background: rgb(49, 210, 242);
  background: linear-gradient(0deg, rgb(49, 210, 242) 0%, rgb(253, 187, 45) 100%);
}
.card-section h2 {
  color: #FFFFFF;
  margin-top: 0;
  padding-top: 2rem;
  font-weight: 500;
}

.card-contaniner {
  display: grid;
  padding-bottom: 2rem;
  justify-items: center;
  align-items: center;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .card-contaniner {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  width: 260px;
  height: 260px;
  border-radius: 2rem;
  overflow: hidden;
  border: 8px solid rgba(13, 202, 240, 0.2);
  position: relative;
}
@media (min-width: 768px) {
  .card {
    width: 280px;
    height: 280px;
  }
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
}

.card-body {
  width: 100%;
  height: 100%;
  top: 0;
  right: -100%;
  position: absolute;
  background: rgba(31, 61, 71, 0.4431372549);
  backdrop-filter: blur(5px);
  border-radius: 15px;
  color: #fff;
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 1.5s;
}
.card-body p {
  color: #FFFFFF;
}

.card:hover .card-body {
  right: 0;
}

.card-title {
  font-size: 1.5rem;
  text-align: left;
  text-transform: uppercase;
  font-weight: bold;
}

.card-subtitle {
  text-transform: capitalize;
  font-size: 1.4rem;
  font-weight: 400;
}

.card-info {
  font-size: 1.6rem;
  line-height: 1.2;
  margin: 10px 0;
  font-weight: 300;
}

.card-btn {
  width: 100%;
  color: #FFFFFF;
  background: #71B100;
  padding: 10px 20px;
  border-radius: 5px;
  text-transform: capitalize;
  outline: none;
  border: none;
  font-size: 1.2rem;
  font-weight: 500;
  cursor: pointer;
}